<template>
    <div class="problem">
        <div class="">
            <p class="title">{{ $t("language.help") }}</p>
            <ul class="content">
                <li class="flexList" v-for="(item, i) of problemList" :key="i">
                    <div class="item">{{ item.questionTypeName }}</div>
                    <div class="right">
                        <p
                            v-for="(it, f) of item.faqList"
                            :key="f"
                            class="listname"
                            @click="openDateil(it)"
                        >
                            {{ it.questionTitle }}
                        </p>
                    </div>
                </li>
            </ul>
        </div>
    </div>
</template>

<script>
export default {
    data() {
        return {
            problemList: [],
        };
    },
    created() {
        this.$http.post(this.$api.getProblemList).then((res) => {
            console.log(res);
            if (res.code == 0) {
                this.problemList = res.data;
            }
        });
    },
    methods: {
        openDateil(data) {
            this.$store.commit("setProblems", data);
            this.$router.push("/help");
        },
    },
};
</script>

<style lang="scss" scoped>
.problem {
    margin: 10px 18%;
    .title {
        color: #2d2b4d;
        font-size: 16px;
        margin: 20px 0;
        font-weight: 500;
    }
    .content {
        background: #fff;
        padding: 24px 70px;
        list-style: none;
        display: flex;
        flex-wrap: wrap;
        .flexList {
            display: flex;
            align-items: center;
            width: 50%;
            margin-bottom: 35px;
            .item {
                color: #2d2b4d;
                font-size: 18px;
                font-weight: 500;
                width: 90px;
                flex-shrink: 0;
                display: -webkit-box;
                -webkit-box-orient: vertical;
                -webkit-line-clamp: 2;
                overflow: hidden;
                letter-spacing: 0;
                text-overflow: ellipsis;
            }
            .right {
                flex-grow: 2;
                margin-left: 50px;
                display: grid;
            }
            .listname {
                margin: 10px;
                cursor: pointer;
                color: var(--themeColor);
                flex-shrink: 1;
                width: 100%;
                display: block;
                text-overflow: ellipsis;
                white-space: nowrap;
                overflow: hidden;
            }
        }
    }
    p {
        margin: 0;
    }
    .content::-webkit-scrollbar {
        /*滚动条整体样式*/
        width: 5px; /*高宽分别对应横竖滚动条的尺寸*/
        height: 1px;
    }
    .content::-webkit-scrollbar-thumb {
        /*滚动条里面小方块*/
        border-radius: 10px;
        background-color: var(--themeColor);
    }
    .content::-webkit-scrollbar-track {
        /*滚动条里面轨道*/
        box-shadow: inset 0 0 5px rgba(0, 0, 0, 0.2);
        background: #f4f4f4;
        border-radius: 10px;
    }
}
</style>